.mantran-sql-views {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  color: #333;
  padding: 20px
}

.mantran-sql-views section {
  margin-bottom: 2rem;
}

.mantran-sql-views h3 {
  color: #c53030;
  border-bottom: 2px solid #c53030;
  padding-bottom: 0.5rem;
}

.mantran-sql-views p {
  margin: 0.5rem 0;
}

.mantran-sql-views .code-block {
  background-color: #f4f4f4;
  padding: 1rem;
  border-radius: 4px;
  font-family: monospace;
  overflow-x: auto;
}

.mantran-sql-views .data-table {
  width: 100%;
  border-collapse: collapse;
  margin: 1rem 0;
}

.mantran-sql-views .data-table th,
.mantran-sql-views .data-table td {
  border: 1px solid #ddd;
  padding: 0.5rem;
  text-align: left;
}

.mantran-sql-views .data-table th {
  background-color: #c53030;
  color: white;
}