.generic-content {
  padding: 1rem;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.generic-content h2 {
  margin-top: 0;
}

.generic-content .content {
  margin: 1rem 0;
}

.generic-content .attachments {
  margin-top: 1rem;
}

.generic-content .download-button {
  display: inline-block;
  margin: 0.5rem 0;
  padding: 0.75rem 1.5rem;
  background-color: #28a745;
  color: white;
  text-decoration: none;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: bold;
  transition: background-color 0.3s, transform 0.2s;
  text-align: center;
}

.generic-content .download-button:hover {
  background-color: #218838;
  transform: scale(1.05);
}

.generic-content .download-button:active {
  background-color: #1e7e34;
  transform: scale(0.95);
}